  display: list-item;
}

/*
 * Remove outline for editable content.
 */
[contenteditable]:focus {
  outline: auto;
}

/* Tables */
/* ============================================ */
/**
1. Correct table border color inheritance in all Chrome and Safari.
*/
table {
  border-color: inherit; /* 1 */
  border-collapse: collapse;
}

caption {
  text-align: left;
}

td,
th {
  vertical-align: top;
  padding: 0;
}

th {
  text-align: left;
  font-weight: bold;
}

.l-header {
  position: fixed;
  top: 0;
  left: 0%;
  z-index: 1;
  width: 100%;
  height: 4.5rem;
}
@media (min-width: 992px) {
  .l-header {
    width: 18.75rem;
    height: 100vh;
  }
}
.l-header__body {
  padding: 0.5625rem 1.125rem;
}

.l-nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 18.75rem;
  height: 100vh;
  z-index: 3;
  transform: translateX(-300px);
  transition: transform 0.3s;
}
@media (min-width: 992px) {
  .l-nav {
    position: static;
    width: auto;
    height: auto;
    transform: none;
  }
}
.l-nav__list {
  margin: 2.1875rem 0 0 1.5625rem;
}
.l-nav__item:not(:first-of-type) {
  margin-top: 1.25rem;
}
.l-nav__link {
  font-size: 0.875rem;
}
.l-nav__sns-list {
  margin: 3.125rem 0 0 1.5625rem;
}
.l-nav__sns-item {
  margin-top: 1.25rem;
}
.l-nav__sns-link {
  font-size: 0.875rem;
}

.l-hamburger-menu {
  position: fixed;
  top: 1.4375rem;
  right: 1.125rem;
  width: 1.875rem;
  height: 1.625rem;
  z-index: 4;
}
.l-hamburger-menu__line {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 1.875rem;
  height: 0.125rem;
  transition: all 0.3s;
}
.l-hamburger-menu__line:nth-child(1) {
  top: calc(50% - 10px);
}
.l-hamburger-menu__line:nth-child(3) {
  top: calc(50% + 10px);
}

.l-mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 2;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s;
}

.l-video-body {
  height: 80vh;
}
@media (min-width: 992px) {
  .l-video-body {
    width: 100%;
    height: 100vh;
  }
}

.l-video {
  position: fixed;
  top: 4.5rem;
  height: 45rem;
  z-index: -10;
  left: 0;
  right: 0;
  margin: 0 auto;
}
@media (min-width: 992px) {
  .l-video {
    top: -6.25rem;
  }
}

.l-sp-only {
  display: block;
}
@media (min-width: 992px) {
  .l-sp-only {
    display: none;
  }
}

.l-footer {
  position: relative;
  height: 10.4375rem;
}
.l-footer__nav {
  padding-top: 3.125rem;
}
.l-footer__nav-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.l-footer__nav-item {
  margin-bottom: 0.625rem;
}
.l-footer__nav-link {
  white-space: nowrap;
  font-size: 0.875rem;
  padding: 0 1.25rem;
}
.l-footer__copy {
  position: absolute;
  left: 50%;
  bottom: 1.25rem;
  transform: translateX(-50%);
  font-size: 0.75rem;
  white-space: nowrap;
}

.l-project {
  padding: clamp(3.75rem, 3.4615384615rem + 1.2307692308vw, 5rem) 0;
}
.l-project__body {
  margin-top: clamp(1.875rem, 1.4423076923rem + 1.8461538462vw, 3.75rem);
}
.l-project__wrapper {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  justify-content: center;
  row-gap: 1.875rem;
}
@media (min-width: 992px) {
  .l-project__wrapper {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 3.5rem;
  }
}
.l-project__image {
  position: relative;
}
.l-project__caption {
  position: absolute;
  bottom: 0.25rem;
  left: 0.4375rem;
  font-size: 0.75rem;
}

.l-feature {
  padding: clamp(3.75rem, 3.4615384615rem + 1.2307692308vw, 5rem) 0 clamp(7.5rem, 6.9230769231rem + 2.4615384615vw, 10rem) 0;
}
.l-feature__body {
  margin-top: clamp(1.875rem, 1.4423076923rem + 1.8461538462vw, 3.75rem);
}
.l-feature__wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}
.l-feature__circle-box:not(:first-child) {
  margin-left: clamp(0.9375rem, 0.5336538462rem + 1.7230769231vw, 2.6875rem);
}
.l-feature__circle {
  position: relative;
  width: clamp(6.25rem, 3.9711538462rem + 9.7230769231vw, 16.125rem);
  height: clamp(6.25rem, 3.9711538462rem + 9.7230769231vw, 16.125rem);
  border-radius: 50%;
}
.l-feature__circle-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 0.875rem;
}
.l-feature__text-body {
  margin-top: 0.9375rem;
  display: grid;
  row-gap: 0.3125rem;
  text-align: center;
}
.l-feature__text-desc {
  white-space: nowrap;
  font-size: 0.875rem;
}

.l-flow {
  padding: 3.75rem 0;
}
.l-flow__body {
  margin-top: clamp(1.875rem, 1.4423076923rem + 1.8461538462vw, 3.75rem);
}
.l-flow__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 992px) {
  .l-flow__wrapper {
    flex-direction: row;
    justify-content: space-between;
  }
}
@media (min-width: 992px) {
  .l-flow__steps-body {
    width: 25%;
  }
}
@media (min-width: 992px) {
  .l-flow__steps-desc-body {
    width: 75%;
  }
}
.l-flow__steps-box:not(:last-child) {
  margin-bottom: 1.875rem;
}
.l-flow__steps-box {
  position: relative;
}
.l-flow__steps {
  position: relative;
  z-index: 2;
  max-width: 12.5rem;
  height: 6.875rem;
  margin: 0 auto;
  clip-path: polygon(50% 100%, 0% 70%, 0% 0%, 100% 0%, 100% 70%); /* 先端の方向を逆に設定 */
}
.l-flow__steps-wrapper {
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.l-flow__steps-bar {
  position: absolute;
  left: 50%;
  z-index: 1;
  transform: translateX(-50%);
  bottom: -1.875rem;
  width: 3.125rem;
  height: 3.125rem;
}
.l-flow__steps-desc-body {
  margin-top: 3.125rem;
}
@media (min-width: 992px) {
  .l-flow__steps-desc-body {
    margin-top: 0;
  }
}
.l-flow__steps-desc {
  display: flex;
}
.l-flow__steps-desc:not(:last-child) {
  margin-bottom: 2.6875rem;
}
.l-flow__steps-desc-number {
  display: inline-block;
  text-align: center;
  width: 2.1875rem;
  height: 2.1875rem;
  border-radius: 50%;
  padding: 0.5rem 0.8125rem;
}
.l-flow__steps-desc-wrapper {
  margin-left: 2.8125rem;
}
.l-flow__steps-desc-title {
  font-size: 1.25rem;
  line-height: 1.5;
  width: 100%;
}
.l-flow__steps-desc-text-body {
  margin-top: 0.625rem;
}

.l-contact {
  padding: clamp(3.75rem, 3.4615384615rem + 1.2307692308vw, 5rem) 0 clamp(7.5rem, 6.9230769231rem + 2.4615384615vw, 10rem);
}
.l-contact__body {
  margin-top: clamp(1.875rem, 1.4423076923rem + 1.8461538462vw, 3.75rem);
}
.l-contact__wrapper {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  row-gap: 1.25rem;
  justify-items: center;
}
@media (min-width: 768px) {
  .l-contact__wrapper {
    grid-template-columns: repeat(2, 1fr);
    column-gap: clamp(1.4375rem, 0.6582792208rem + 1.6233766234vw, 2.6875rem);
  }
}
@media (min-width: 992px) {
  .l-contact__wrapper {
    grid-template-columns: repeat(3, 1fr);
    row-gap: 0;
  }
}
.l-contact__info-box {
  position: relative;
  width: clamp(12.5625rem, 11.7259615385rem + 3.5692307692vw, 16.1875rem);
  height: 11.8125rem;
}
.l-contact__info-image {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.l-contact__info-box-triangle {
  position: absolute;
  right: 0.25rem;
  bottom: 0.25rem;
  width: 1.25rem;
  height: 1.25rem;
  clip-path: polygon(100% 0, 0% 100%, 100% 100%);
}

@media (min-width: 992px) {
  .l-content {
    padding-left: 18.75rem;
  }
}

.m-section-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: clamp(1.25rem, 1.1346153846rem + 0.4923076923vw, 1.75rem);
  font-weight: bold;
  white-space: nowrap;
  padding: 0 0.625rem;
}
@media (min-width: 992px) {
  .m-section-title {
    padding: 0;
  }
}

.m-section-title::before,
.m-section-title::after {
  border-top: 1px solid;
  content: "";
  width: 20%;
}
@media (min-width: 992px) {
  .m-section-title::before,
  .m-section-title::after {
    width: 28%;
  }
}

.m-section-subhead {
  margin-top: clamp(1.875rem, 1.4423076923rem + 1.8461538462vw, 3.75rem);
}

.m-container {
  max-width: 56.25rem;
  margin: 0 auto;
  padding: 0 1.25rem;
}

.l-nav.is-active {
  transform: none;
}

.l-hamburger-menu.is-active > .l-hamburger-menu__line:nth-child(1) {
  top: 50%;
  transform: translate(-50%, -50%) rotate(-315deg);
}

.l-hamburger-menu.is-active > .l-hamburger-menu__line:nth-child(2) {
  opacity: 0;
}

.l-hamburger-menu.is-active > .l-hamburger-menu__line:nth-child(3) {
  top: 50%;
  transform: translate(-50%, -50%) rotate(315deg);
}

.l-mask.is-active {
  visibility: visible;
  opacity: 1;
}

:root {
  --base-color:#FFF;
  --text-color:#121212;
  --mask-color:#313131;
  --stepBg-color:#414141;
  --stepBar-color:#FFDD00;
}

body {
  color: var(--text-color);
}

.theme-header {
  background-color: var(--base-color);
}

.theme-hamburger-menu-line {
  background-color: var(--text-color);
}

.theme-nav {
  background-color: var(--base-color);
}
@media (min-width: 992px) {
  .theme-nav {
    background-color: transparent;
  }
}

.theme-mask {
  background-color: var(--mask-color);
}

.theme-section-title {
  color: var(--base-color);
}

.theme-project-bg {
  background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8));
}

.theme-project-caption {
  color: var(--base-color);
}

.theme-feature-bg {
  background-color: rgba(0, 0, 0, 0.8);
}

.theme-feature-circle-color {
  color: var(--base-color);
}

.theme-feature-circle-border {
  border: 0.0625rem solid var(--base-color);
}

.theme-flow-bg {
  background-color: var(--base-color);
}

.theme-flow-steps-bg {
  background-color: var(--stepBg-color);
  color: var(--base-color);
}

.theme-flow-steps-bar {
  background-color: var(--stepBar-color);
}

.theme-steps-desc-number-bg {
  background-color: var(--stepBg-color);
  color: var(--base-color);
}

.theme-flow-steps-desc-title {
  border-bottom: 0.0625rem solid var(--text-color);
}

.theme-contact-bg {
  background-color: var(--base-color);
}

.theme-contact-info-box {
  border: 0.0625rem solid var(--text-color);
}

.theme-contact__info-box-triangle {
  background-color: var(--text-color);
}

.theme-footer {
  background-color: var(--text-color);
  color: var(--base-color);
}

.theme-footer__nav-item {
  border-right: 0.0625rem solid var(--base-color);
}