@use"../base/_mixin.scss" as *;
// body
body{
    color: var(--text-color);
}

// header
.theme-header{
    background-color: var(--base-color);
}

// hamburger-menu
.theme-hamburger-menu-line{
  background-color: var(--text-color);
}

// nav
.theme-nav{
    background-color:var(--base-color);
    @include responsive("tablet"){
        background-color: transparent;
    }
}

// mask
.theme-mask{
    background-color: var(--mask-color);
}

// section-title
.theme-section-title{
  color: var(--base-color);
}

// project
.theme-project-bg{
    background: linear-gradient(rgba(0,0,0,0),rgba(0,0,0,0.8));
}

.theme-project-caption{
     color: var(--base-color);
}

// feature
.theme-feature-bg{
    background-color: rgba(0, 0, 0, 0.8);
}

.theme-feature-circle-color{
    color: var(--base-color);
}


.theme-feature-circle-border{
    border: px-to-rem(1px) solid var(--base-color);
}

// flow

.theme-flow-bg{
    background-color: var(--base-color);
}

.theme-flow-steps-bg{
  background-color: var(--stepBg-color);
  color: var(--base-color);
}
.theme-flow-steps-bar{
    background-color: var(--stepBar-color)
}

.theme-steps-desc-number-bg{
    background-color: var(--stepBg-color);
    color: var(--base-color);
}

.theme-flow-steps-desc-title{
    border-bottom: px-to-rem(1px) solid var(--text-color);
}

// contact
.theme-contact-bg{
 background-color: var(--base-color);
}

.theme-contact-info-box{
    border: px-to-rem(1px) solid var(--text-color);
}

.theme-contact__info-box-triangle{
    background-color: var(--text-color);
}

// footer
.theme-footer{
    background-color: var(--text-color);
    color: var(--base-color);
}
.theme-footer__nav-item{
    border-right: px-to-rem(1px) solid var(--base-color);
}

